import React from "react";

import { createGlobalStyle } from "styled-components";
import { Row, Col, Container/* , Button */ } from 'react-bootstrap';
/* import { FaCloudDownloadAlt } from 'react-icons/fa'; */
import config from "../../Config/config";
import { QRCode } from 'react-qrcode-logo';
import ReactToPrint from 'react-to-print';

import Barcode from "react-barcode"
import PropTypes from "prop-types";

class ComponentToPrint extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            checkbox: false,
            dropdownValue: "fa"
        };
    }

    changeDropdown(event) {
        this.setState({ dropdownValue: event.target.value });
    }

    render() {
        return (
            <div style={{ paddingTop: 30, paddingLeft: 15 }}>
                <Row>
                    {
                        this.props.Template.map(rows => (
                            <Col xs={4} >
                                <div style={{ paddingTop: 30, paddingLeft: 15 }}>

                                    <p>  {rows.id}</p>
                                 
                                    <div>
                                     <Barcode value= {rows.colis.index_colis}  />
                                     </div>
                                    <p> code colis :  {rows.colis.index_colis}</p>
                                    <p> address :  {rows.colis.address}</p>
                                    <p> Telph :  {rows.colis.phone_number}</p>


                                </div>
                            </Col>
                        ))


                    }
                </Row>
            </div>
        );
    }
}

class Example extends React.Component {
    render() {
        return (
            <div style={{ alignContent: "center", alignItems: "center", }} >
                <ReactToPrint
                    trigger={() => <button className="btn btn-primary"><span className="fa fa-print"></span>Impression </button>}
                    content={() => this.componentRef}
                />
                <ComponentToPrint ref={el => (this.componentRef = el)} Template={this.props.Template} providerinfo={this.props.providerinfo} />
            </div>
        );
    }
}

export default Example;

