import { Button } from "react-bootstrap";
import axios from "axios"
import Card from "../../../App/components/MainCard";
import ModificationModal from './Modal.Modification'
import BootstrapTable from "react-bootstrap-table-next";
import ResponsiveContainer from "react-responsive-widget";
import filterFactory from "react-bootstrap-table2-filter";
import { CSVLink } from "react-csv";
import { Link } from "react-router-dom";
import paginationFactory from "react-bootstrap-table2-paginator";
import TicketAdd from '../../Ticket/Ticket.add'
import TemplatePrint from '../../Template/TemplatePrint'
import TemplatePrints from '../../Template/TemplatePrints'
import TemplatePrintsQRCODE from '../../Template/Templateqrcode/TemplatePrintsQRCODE'
import { confirmAlert } from "react-confirm-alert";
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import
import jwtDecode from "jwt-decode";
import i18n from './../../../i18n';

var urlbackend = require('../../../env/env')();




function Search() {
  const [tableshow, settableshow] = useState([])
  const [selectedRows, setSelectedRows] = useState([])
  const [data, setDataToShow] = useState([])
  const [providerr, setproviderr] = useState([])

  /* const [selectedRows1,setSelectedRows] = useState([]) */

  useEffect(() => {
    getdata_listpocup()
    // eslint-disable-next-line react-hooks/exhaustive-deps

  }, [])
  const getdata_listpocup = () => {

    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };
    const url = urlbackend + "/provider/pickup/pending?current_page=1&per_page=1000";

    axios
      .get(url, config)
      .then((response) => response.data)
      .then((data) => {
        //this.setState({ tableshow:  });

        if (data.data.length !== 0) {
          settableshow(data.data.data)
        } else {

          settableshow([])
        }

        //  console.log(data);
      }).catch(() => {

        console.log('e')
      })
      var m =jwtDecode(localStorage.getItem('token')).id
    
      axios.get(urlbackend + '/provider/info/' +m, config).then((Response) => {
        setproviderr(Response.data)
  
        console.log("Response.data",Response.data)
      }).catch(()=>{
        console.log("he")
      })
  }


  /*  const ImprimeSelectedData =()=>{
     console.log(selectedRows);
     const result = tableshow.filter(({id}) => selectedRows.includes(id))
     console.log(result)
     setDataToShow(result)
 
 
   } */
  const Annulationcolis = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div>

        <Button
          variant="danger"
          onClick={() => Annulercolis(row.id)}
        >
          {" "}
          <span className="fa fa-trash"></span>
        </Button>



      </div>
    );
  };

  const Annulercolis = (id) => {
    confirmAlert({
      title: "Annuler livraison",
      message: "confirmer la suppression définitive?",
      buttons: [
        {
          label: "Oui",
          onClick: () => {
            axios
              .put(urlbackend + "/delete/pickup/" + id)
              .then((res) => {
                console.log(res);
                getdata_listpocup()
                //window.location.reload(true);
              }).catch((e) => {
                console.log(e)
              })
          },
        },
        {
          label: "Non",
          onClick: null,
        },
      ],
    });
    /*  */
  };

  /*  const showDataToImprimer = ()=>{
     return (
       <div>
         <TemplatePrint2 Template={data}  > </TemplatePrint2>
       </div>
 
     );
   }
 
  */
  const expandRow = {
    parentClassName: 'parent-expand-foo',
    renderer: (row, rowIndex) => (
      <div>

        <p>    <span style={{ color: "black", fontWeight: 'bold', textAlign: "center" }}>  {`prix :`}{" "} </span> {row.colis.price} (TND) </p>

        <p >
          <span style={{ color: "black", fontWeight: 'bold', textAlign: "center" }}>  {`Mode de paiment `}{"     :"} </span> {row.colis?.payment_mode} </p>


        <p>
          Description produit: {row.colis.designation}
        </p>

      </div>
    ),
    onlyOneExpanding: true,
  };
  const linkFollow = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div>
        <Link to={"/history/colis/provider/" + row.colis.id}>
          <Button
            variant="success"
            onClick={() => console.log("not yet")}
          >
            {" "}
            <span className="fa fa-file"></span>
          </Button>

        </Link>

      </div>
    );
  };
  const linkFollowtick = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div>

        <TicketAdd colis={row.colis} />

      </div>
    );
  };
  const impression = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div>

        <TemplatePrint Template={row} providerinfo={providerr} > </TemplatePrint>

      </div>
    );
  };
  const Modifier = (cell, row, rowIndex, formatExtraData) => {
    return (
      <div>

        <ModificationModal colismodifier={row?.colis} reloaddata={() => getdata_listpocup()}  > </ModificationModal>

      </div>
    );
  };


  const columns = [
    {
      dataField: "colis.index_colis",
      text: i18n.t('package-number'),


    },
    {
      dataField: "colis.name_complete",
      text: i18n.t('client-name'),


    },
    // {
    //   dataField: "colis.address",
    //   text: "address",

    //   style: { textAlign: "center" },


    //   formatter: (cell, row) => {
    //     return (<div>{`${row.colis.gouvernorat} `} <br></br> {`${row.colis.city}`} {"  "} {`${row.colis.address}`} </div>);
    //   }
    //   // formatter: this.linkFollow,

    // },
    // {
    //   dataField: "colis.phone_number",
    //   text: "N.téléphone",

    // },
    // {
    //   dataField: "colis.designation",
    //   text: "Designation",


    // },
    // {
    //   dataField: "colis.price",
    //   text: "Prix(TND)",

    // },
    {
      dataField: "colis.id",
      text: i18n.t('edit-information'),
      style: { textAlign: "center" },

      formatter: Modifier,

    },
    {
      dataField: "colis.id",
      text: i18n.t('print'),
      style: { textAlign: "center" },

      formatter: impression,

    },

    {
      dataField: "colis.type_envoi",
      text: i18n.t('add-order'),
      style: { textAlign: "center" },
      formatter: linkFollowtick,

    },

    {
      dataField: "colis.type_envoi",
      text: i18n.t('history'),
      style: { textAlign: "center" },
      formatter: linkFollow,

    },

    {
      dataField: "colis.type_envoi",
      text: i18n.t('cancel-name'),
      style: { textAlign: "center" },
      formatter: Annulationcolis,

    },
  ];


  useEffect(() => {
    //console.log(selectedRows);
    const result = tableshow.filter(({ id }) => selectedRows.includes(id))
    // console.log(result)
    setDataToShow(result)

  }, [selectedRows])


  const selecthandel = (row, isSelect, e) => {

    if (isSelect) {
      setSelectedRows((prevSelected) => [...prevSelected, row.id]);
    } else {
      setSelectedRows((prevSelected) => prevSelected.filter((x) => x !== row.id));
    }
  }


  const handleOnSelectAll = (isSelect, rows) => {

    if (isSelect) {
      setDataToShow(rows)
    } else {
      setDataToShow([])
    }
  }

  const selectRow = {
    mode: 'checkbox',
    /* clickToExpand: true, */
    clickToSelect: true,
    style: { background: '#c8e6c9' },
    /*  selected: selectedRows, */
    onSelect: selecthandel,
    onSelectAll: handleOnSelectAll

  };

  /**
 * function render: return virtual dom
 * @param props {string } include the list of props if that change

 */
  return (
  
      <Card   className="card-box mb-8">
        <ResponsiveContainer>
          <div className="app-row">
            <div className="app-col-xs-12 app-col-md-12">
              {/*    <Button
        variant="success"
        onClick={() =>showDataToImprimer()}
      > showDataToImprimer
      </Button> */}
              {/* {data.length} */}
              {data.length > 0 &&
                <TemplatePrints Template={data} />
              }
               {data.length > 0 &&
                <TemplatePrintsQRCODE Template={data} />
              }
              {/* <Button
        variant="success"
        onClick={() =>ImprimeSelectedData()}
      > Select Rows
      </Button> */}
              <BootstrapTable
                striped
                bordered
                hover
                variant="dark"
                expandRow={expandRow}
                bootstrap4={true}
                keyField="id"
                data={tableshow}
                columns={columns}
                selectRow={selectRow}
                pagination={paginationFactory({
                  sizePerPage: 25,
                  sizePerPageList: [5, 10, 15, 20, 25],
                })}
                filter={filterFactory()}
                headerClasses="table thead-light mb-0"
              ></BootstrapTable>

              <CSVLink data={tableshow}><Button
                color="primary"
                variant="contained"

              >

              </Button>

              </CSVLink>


            </div>
          </div>
        </ResponsiveContainer>
      </Card>

  );
}
export default Search;